import { Injectable } from '@angular/core'
import { ActivatedRouteSnapshot, CanActivate, Router, UrlTree } from '@angular/router'
import { Permission } from '../api-interfaces'
import { SessionService } from '../services/session.service'

@Injectable({
    providedIn: 'root',
})
export class PaymentGuard implements CanActivate {
    constructor(private session: SessionService, private router: Router) {}

    public canActivate(route: ActivatedRouteSnapshot): boolean | UrlTree | Promise<UrlTree> {
        if (
            !this.session.user.hasPermission('payment-swift') &&
            !this.session.user.hasPermission('payment-local') &&
            !this.session.user.hasPermission('payment-crypto')
        ) {
            return this.router.parseUrl('/404')
        }
        const method = route.queryParamMap.get('method') || 'swift'
        if (!this.session.user.hasPermission(`payment-${method}` as Permission)) {
            const permissions = this.session.user.permissions
                .filter(permission => permission.startsWith('payment'))
                .map(permission => permission.split('-')[1])
            const paymentPermission =
                permissions.find(permission => permission === 'swift') ||
                permissions.find(permission => permission === 'local') ||
                'crypto'
            return this.router.parseUrl(
                route.data.redirectTo + `${paymentPermission === 'swift' ? '' : '?method=' + paymentPermission}`
            )
        }
        return true
    }
}
