import { ConversionStatus, IConversion, Transaction } from '../../api-interfaces'
import { User } from '../core/user.model'
import { Currency } from './currency.model'

export class Conversion implements IConversion {
    public id: string
    public status: ConversionStatus
    public exchangeRate: string | null
    public user: User
    public currency: Currency
    public baseTransaction: Transaction
    public counterTransaction: Transaction
    public expiresAt: string
    public createdAt: string
    public updatedAt: string

    constructor(data: IConversion) {
        Object.assign(this, data)
        if (data.currency) {
            this.currency = new Currency(data.currency)
        }
        if (data.user) {
            this.user = new User(data.user)
        }
    }

    public get userName(): string {
        return this.user.name
    }
}
