import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http'
import { Injectable } from '@angular/core'
import { Operation } from 'fast-json-patch'
import { Observable } from 'rxjs'
import { map } from 'rxjs/operators'
import { IConversion, RPCResult } from '../../api-interfaces'
import { Conversion } from '../../models/accounting/conversion.model'

export interface ConversionCreatePayload {
    currency: {
        code: string
        [k: string]: any
    }
    baseTransaction: {
        amount: string
        currency: {
            code: string
            [k: string]: any
        }
    }
    user?: {
        id: string
        [k: string]: any
    } | null
}

export interface ConversionOfferPayload {
    exchangeRate: string
    expiresAt: string
}

export interface ConversionDirectPayload {
    exchangeRate: string
}

@Injectable({
    providedIn: 'root',
})
export class ConversionService {
    public static readonly apiUrl = '/conversions'

    constructor(private http: HttpClient) {}

    public fetchById(id: string): Observable<Conversion> {
        return this.http
            .get<IConversion>(`${ConversionService.apiUrl}/${id}`)
            .pipe(map(payload => new Conversion(payload)))
    }

    public create(conversionPayload: ConversionCreatePayload): Observable<Conversion> {
        return this.http
            .post<IConversion>(ConversionService.apiUrl, conversionPayload)
            .pipe(map(payload => new Conversion(payload)))
    }

    public patch(id: string, operations: Operation[]): Observable<Conversion> {
        return this.http
            .patch<Conversion>(`${ConversionService.apiUrl}/${id}`, operations, {
                headers: new HttpHeaders({ 'Content-Type': 'application/json-patch+json' }),
            })
            .pipe(map(payload => new Conversion(payload)))
    }

    public makeOffer(id: string, params: ConversionOfferPayload): Observable<Conversion> {
        return this.http
            .post<RPCResult<IConversion>>(
                `${ConversionService.apiUrl}/${id}`,
                {
                    id: Math.round(Math.random() * 10000),
                    jsonrpc: '2.0',
                    method: 'makeOffer',
                    params,
                },
                {
                    headers: new HttpHeaders({
                        'Content-Type': 'application/json-rpc',
                    }),
                }
            )
            .pipe(map(rpcResult => new Conversion(rpcResult.result)))
    }

    public directlyConvert(id: string, params: ConversionDirectPayload): Observable<Conversion> {
        return this.http
            .post<RPCResult<IConversion>>(
                `${ConversionService.apiUrl}/${id}`,
                {
                    id: Math.round(Math.random() * 10000),
                    jsonrpc: '2.0',
                    method: 'directlyConvert',
                    params,
                },
                {
                    headers: new HttpHeaders({
                        'Content-Type': 'application/json-rpc',
                    }),
                }
            )
            .pipe(map(rpcResult => new Conversion(rpcResult.result)))
    }

    public accept(id: string): Observable<Conversion> {
        return this.http
            .post<RPCResult<IConversion>>(
                `${ConversionService.apiUrl}/${id}`,
                {
                    id: Math.round(Math.random() * 10000),
                    jsonrpc: '2.0',
                    method: 'accept',
                },
                {
                    headers: new HttpHeaders({
                        'Content-Type': 'application/json-rpc',
                    }),
                }
            )
            .pipe(map(rpcResult => new Conversion(rpcResult.result)))
    }

    public count(where: any, urlPrefix = ''): Observable<number> {
        return this.http
            .head(`${urlPrefix}${ConversionService.apiUrl}`, {
                observe: 'response',
                params: new HttpParams().set('where', JSON.stringify(where)),
            })
            .pipe(
                map(response =>
                    response.headers && response.headers.has('x-total-count')
                        ? ~~response.headers.get('x-total-count')!
                        : 0
                )
            )
    }

    public countByUserId(userId: string, where: any): Observable<number> {
        return this.count(where, `/users/${userId}`)
    }
}
