import { Injectable } from '@angular/core'
import { Observable } from 'rxjs'
import { flatMap } from 'rxjs/operators'
import { SessionService, User } from './session.service'
import { TokenOptions, TokenService } from './token.service'

@Injectable({
    providedIn: 'root',
})
export class LoginService {
    constructor(private session: SessionService, private tokenService: TokenService) {}

    public login(options: TokenOptions): Observable<User> {
        return this.tokenService
            .fetch(options)
            .pipe(flatMap(({ refreshToken, token }) => this.session.login(refreshToken, token)))
    }
}
